import { React } from 'react';
import { Link } from 'react-router-dom';
import "./TeamTile.scss"

export const TeamTile = ({ teamName }) => {
    if (!teamName) return null;
    return (
        <div className="TeamTile">
            <h1><Link to={`/teams/${teamName}`}>{teamName}</Link></h1>
        </div>
    );
}

